export default {
  name: 'mv-list',
  data() {
    return {
      currentMv: null,
      playing: false,
      mvReady: false,
      currentVdo: null,
      error: false,
      fullscreen: false,
      orientation: 0
    }
  },
  props: {
    data: {
      type: Array,
      default: () => []
    }
  },
  created() {
    // this.$emit('on-key', this.keys)
  },
  mounted() {
    $(document).bind(
      'fullscreenchange webkitfullscreenchange mozfullscreenchange',
      () => {
        //todo code
        this.fullscreen =
          document.webkitIsFullScreen &&
          document.webkitCurrentFullScreenElement === this.currentVdo
      }
    )
  },
  watch: {
    'currentMv.videoType.r': function(newR) {
      // body...
      if (newR) {
        this.reloadVideo()
      }
    },
    // body...
    currentVdo(newVdo, oldVdo) {
      if (newVdo) {
        newVdo.load()
        $(newVdo).attr('controls', 'controls')

        this.reactive.$scroll.scrollToElement(newVdo, 1000, true, true)
      }
      if (oldVdo) {
        oldVdo.src = ''
        $(oldVdo).removeAttr('controls')
      }
    },
    'reactive.scrollY': function(newY) {
      if (!this.currentVdo) {
        return
      }
      // body...
      const top = this.getVdoRect().top
      const bottom = this.getVdoRect().bottom
      const clientTop = this.reactive.clientTop
      // console.log(top)
      if (bottom <= clientTop || top >= window.innerHeight) {
        this.currentVdo.src = ''

        this.currentVdo = null
        this.currentMv = null
      }
    }
  },
  inject: ['reactive'],
  inheritAttrs: false,
  computed: {
    videoType() {
      return this.currentMv ? this.currentMv.videoType : null
    },
    selected() {
      return this.currentMv
        ? { r: this.currentMv.videoType.r, index: this.selectedIndex[0] }
        : null
    },
    r() {
      return this.currentMv ? this.currentMv.videoType.r : -1
    },
    selectedIndex() {
      return this.currentMv ? [this.currentMv.videoType.index] : [-1]
    }
  },
  methods: {
    isCurrent(item) {
      return this.currentMv === item
    },

    showPicker() {
      if (!this.picker) {
        this.picker = this.$createPicker({
          title: 'Picker',
          $props: {
            selectedIndex: 'selectedIndex'
          },
          alias: {
            value: 'r'
          },
          data: [this.$attrs.pickerData],
          onSelect: this.selectHandle
          // onCancel: this.cancelHandle
        })
      }
      this.picker.show()
    },
    selectHandle(selectedVal, selectedIndex, selectedText) {
      this.currentMv.videoType.index = selectedIndex[0]
      this.currentMv.videoType.text = selectedText[0]
      this.currentMv.videoType.r = selectedVal[0]
    },
    reloadVideo() {
      let _urlObj = this.parse(this.currentMv.url)
      _urlObj.r = this.currentMv.videoType.r
      let url = this.unescape(this.stringify(_urlObj))
      this.currentVdo.src = url
    },

    onloadstart(e) {
      if (e.target !== this.currentVdo) {
        return
      }
      this.errToast && this.errToast.close()
      this.mvReady = false
      this.error = false
    },
    onerror(e) {
      if (e.target !== this.currentVdo) {
        return
      }
      this.error = true
      this.errToast = this.Toast({
        message: 'error',
        duration: 2000
      })
    },
    getVdoRect() {
      return this.currentVdo ? this.currentVdo.getBoundingClientRect() : {}
    },
    onwaiting() {
      this.mvReady = false
    },
    oncanplay(e) {
      this.mvReady = true
      this.currentVdo && this.currentVdo.play()
      this.playing = true
      // this.
    },
    selectItem(e, mv) {
      if (this.currentVdo === e.target) {
        return
      }
      let _urlObj = this.parse(mv.url)
      _urlObj.r = mv.videoType.r
      let url = this.unescape(this.stringify(_urlObj))
      mv._url = url
      e.target.src = url

      this.currentVdo = e.target
      this.currentMv = mv
    }
  }
}



